@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600;700;800;900&display=swap);
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-size: 1vmax;

}
body{
  background: #10141b;
  overflow-x: hidden;
}

::selection {
  background:rgba(5, 255, 234, 0.356);
}

::-webkit-scrollbar {
  width: 8px;
  height: 6px;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: transparent; 
  border-radius: 50vmax;
}
 
::-webkit-scrollbar-thumb {
  background: linear-gradient(315deg,  rgb(0, 255, 191) 0%, rgb(0, 255, 191) 100%); 
  border-radius: 50vmax;
}

:-ms-input-placeholder { 
  color: rgb(0, 255, 191);
  opacity: 1; 
}

::placeholder { 
  color: rgb(0, 255, 191);
  opacity: 1; 
}

:-ms-input-placeholder { 
  color: rgb(0, 255, 191);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-text-fill-color: white;
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}


/* loading backdrop  */

.LoadingBackdrop{
  display: flex;
  position: fixed;
  background: rgba(82, 82, 82, 0.404);
  top: 0;
  left: 0;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  align-items: center;
}
a{
  text-decoration: none;
  color: white;
}
.header{
    margin-top: 2.5vmax;
    display: flex;
    justify-content: space-between;
}

.left>a>img{
    margin-left: 5vmax;
   width: 160px;
   margin-top: -.5vmax;  
}
.navlinks>a>button{
    margin: auto 5vmax;
    width: 5vmax;
    height: 2.5vmax;
    margin-top: -8vmax;
    border:2px solid rgb(0, 255, 191);
    background: transparent;
    cursor: pointer;
    color: rgb(0, 255, 191);
    transition: .5s;
}
.navlinks>a>button:hover{
  
    background:rgba(0, 255, 191, 0.13);
  
}
.navlinks{
    margin-top: .8vmax;
    display: flex;

}
.navlinks>a{
    margin-left: 5vmax;
    text-decoration: none;
    color: rgb(0, 255, 191);
    font-size: 1.4vmax;
    transition: .5s;
}
