    outline: none;
    border: none;
    height: 2vmax;
    width: 15vmax;
    padding: 0;
    color: rgb(0, 255, 191);
    background: transparent;
    border-bottom: 1px solid rgb(0, 255, 191);
}
.search>button{
    outline: none;
    border: none;
    position: absolute;
    right: 0;
    top: .2vmax;
    background: transparent;
    color: rgb(0, 255, 191);
    cursor: pointer;
}
.profile{
    position: relative;
}
.avatar>a>img{
    border-radius: 50%;
    margin: auto 5vmax;
    transform: translateY(-.5vmax);
    height: 3vmax;
    width: 3vmax;
    object-fit: cover;

}

.accMenu{
    flex-direction: column;
    color:white;
    background: rgba(0, 0, 0, 0.671);
    width: 10vmax;
    padding: 1vmax;
    list-style: none;
    text-align: center;
    position: absolute;
    right: 12%;
    transition: 1s;
   display: none;
}
.accMenu>a{
    margin: .5vmax auto;
    width:100%;
    transition: .5s;
}
.accMenu>a:hover,.accMenu>a>button:hover{
   color: rgb(0, 255, 115);
}
.accMenu>a>svg,.accMenu>a>button>svg{
    transform: translateY(.45vmax);
}
.accMenu>a>button{
    background: none;
    color: white;
    border: none;
    cursor: pointer;
    transition: .5s;
}
.avatar:hover+.accMenu{
    display: flex;
}
.accMenu:hover{
    display: flex;
}

